.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet {
        align-items: flex-end;
    }
    .dark-mode-boton {
        width: 3rem;
        margin-top: .7rem;
        margin-bottom: 1rem;
        color: $verde_whatsapp;
        filter: invert(100%);
        &:hover {
            filter: invert(70%);
            cursor: pointer;
        }
        @include tablet {
            margin-top: 0;
        }
    }
}

.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 0rem;
    color: $negro;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
    .logotipo {
        /* Arriba | Horizontal | Abajo */
        margin: 0rem auto 0rem;
        width: 45%;
        &:hover {
            filter: opacity(85%);
            cursor: pointer;
        }
        // height: 50px;
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0px;
    margin: 0 0;
    margin-top: .1rem;
    margin-bottom: 2rem;
    color: $negro;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        font-family: $open_sans;
        font-weight: 600;
        color: $blanco;
        text-decoration: none;
        display: block;
        text-align: center;
        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type {
                margin-right: 0;
            }
        }
        &:hover {
            color: $verde_whatsapp;
        }
    }
}


/** Menu de hamburguesa **/

.mobile-menu {
    @include tablet {
        display: none;
    }
    img {
        // filter: invert(100%);
        width: 3rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
    }
    &:hover {
        filter: invert(70%);
        cursor: pointer;
    }
}

.mostrar {
    visibility: visible;
    opacity: 2;
    height: auto;
}