.header {
    // background: $blanco;
    // background: rgb(0, 0, 0);
    // background: linear-gradient(0deg, rgba(0, 0, 0, 0.8198033022076896) 0%, rgba(255, 255, 255, 1) 7%);
    // background: linear-gradient(-45deg, $blanco, #FFC300, $blanco, $blanco);
    background: linear-gradient(-45deg, $negro, $negro, $verde_whatsapp, $verde_whatsapp);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    @keyframes gradient {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0% 50%;
        }
    }
    // padding: 0 0 1.4rem 0;
    &.inicio {
        background-position: center center;
        background-size: cover;
        @include tablet {
            min-height: 70rem;
        }
        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}