    /*:::::Pie de Pagina*/
    
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: $open_sans;
    }
    
    .pie-pagina {
        width: 100%;
        margin-top: 10px;
        background-color: $negro;
        .grupo-1 {
            width: 100%;
            max-width: 1200px;
            margin: auto;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 50px;
            padding: 45px 0px;
            .box {
                figure {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img {
                        width: 250px;
                    }
                }
                h2 {
                    font-family: $open_sans;
                    color: $blanco;
                    margin-bottom: 25px;
                    font-size: 15px;
                    font-weight: $bold;
                    text-align: left;
                }
                p {
                    font-family: $open_sans;
                    color: $blanco;
                    margin-bottom: 10px;
                    font-size: 17px;
                    text-align: left;
                }
            }
            .red-social {
                a {
                    display: inline-block;
                    text-decoration: none;
                    width: 48px;
                    height: 48px;
                    line-height: 52px;
                    color: $blanco;
                    margin-right: 10px;
                    background-color: $grisOscuro;
                    text-align: center;
                    transition: all 300ms ease;
                    &:hover {
                        background-color: darken($verde_whatsapp, 5);
                        cursor: pointer;
                    }
                }
            }
        }
        .grupo-2 {
            background: linear-gradient( $negro, $grisOscuro);
            padding: 15px 10px;
            text-align: center;
            color: $blanco;
            small {
                font-size: 15px;
            }
        }
    }
    
    @media screen and (max-width:800px) {
        .pie-pagina {
            .grupo-1 {
                width: 90%;
                grid-template-columns: repeat(1, 1fr);
                grid-gap: 30px;
                padding: 35px 0px;
            }
        }
    }
    
