/** Media Queries **/

@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc( $separacion / 2);
    display: $display;
    border: none;
    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}

@mixin botonflotante($bgColor, $textColor) {
    font-size: 16px;
    /* Cambiar el tama√±o de la tipografia */
    text-transform: uppercase;
    /* Texto en mayusculas */
    font-weight: $bold;
    /* Fuente en negrita o bold */
    color: $textColor;
    /* Color del texto */
    border-radius: 5px;
    /* Borde del boton */
    letter-spacing: 2px;
    /* Espacio entre letras */
    background-color: $bgColor;
    /* Color de fondo */
    padding: 18px 30px;
    /* Relleno del boton */
    position: fixed;
    bottom: 40px;
    right: 40px;
    transition: all 300ms ease 0ms;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    z-index: 99;
    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}