.boton-amarillo {
    @include boton($amarillo, inline-block);
}

.boton-verde {
    @include boton($verde_whatsapp, inline-block);
}

.boton-verde-block {
    @include boton($verde_whatsapp, block);
}

.boton-amarillo-block {
    @include boton($amarillo, block);
}

.boton-verde {
    @include boton($verde, inline-block);
}

@include tablet {
    .btn-flotante {
        font-size: 14px;
        padding: 12px 20px;
        bottom: 20px;
        right: 20px;
    }
}

// boton flotante
.btn-Whatsapp-flotante {
    width: 68px;
    position: fixed;
    bottom: 10px;
    right: 5px;
    &:hover {
        filter: opacity(75%);
        cursor: pointer;
    }
}

@include tablet {
    .btn-Whatsapp-flotante {
        width: 58px;
    }
}